.menu__link:hover {
  transform: translateY(-2px);
  color: #125D98;
}

.bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 8px 0;
  height: 60px;
}

.social__list {
  padding: 0 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.social__item {
  width: 32px;
  height: 32px;
}

.social__item:not(:last-child) {
  margin-right: 15px;
}

.social__icon {
  max-width: 32px;
  max-height: 32px;
  transition: transform 0.4s;
}

.social__icon:hover {
  transform: scale(1.1);
}

.search {
  padding: 0 15px;
}

.search__form {
  display: flex;
  align-items: center;
  max-width: 250px;
  border-bottom: 2px solid black;
}

.search__input {
  margin-right: 15px;
  min-width: 80%;
  height: 24px;
  font-style: 14px;
  padding: 2px;
}

.search__btn {
  width: 28px;
  height: 28px;
  cursor: pointer;
  transition: transform 0.4s;
}

.search__btn:hover {
  transform: scale(1.1);
}

.search__icon {
  max-width: 22px;
  max-height: 22px;
  transition: transform 0.4s;
}

.search__icon:hover {
  transform: scale(1.1);
}

.content {
  border: 4px solid #125D98;
  margin-bottom: 25px;
  padding: 25px;
  border-radius: 12px;
  background-color: #fff;
}

.content__inner {
  display: grid;
  grid-gap: 30px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
}

.content__item {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 12px;
  border-radius: 6px;
  background-image: linear-gradient(to top, #f3f9fb, #eaf6fb, #e2f3fb, #daf0fc, #d2edfd, #d1edfe, #d1ecfe, #d0ecff, #d5eeff, #dbf0ff, #e0f2ff, #e5f4ff);
  transition: transform 0.4s, box-shadow 0.4s;
}

.content__item:first-child {
  grid-column: 1/3;
  grid-row: 1/3;
  padding: 20px;
  max-width: 100%;
  max-height: 100%;
}

.content__item:first-child .content__img-container {
  margin-bottom: 22px;
}

.content__item:first-child .content__title {
  margin-bottom: 15px;
  font-size: 24px;
}

.content__item:first-child .content__img {
  width: 100%;
  height: 360px;
}

.content__item:first-child .content__text {
  display: block;
  margin-bottom: 26px;
  font-size: 16px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -moz-box;
  -moz-box-orient: vertical;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  line-clamp: 4;
  box-orient: vertical;
}

.content__item:hover {
  box-shadow: 0 5px 20px 5px rgba(0, 0, 0, 0.4);
  transform: translateY(-4px);
}

.content__link {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  cursor: pointer;
}

.content__img {
  width: 278px;
  height: 170px;
  margin-bottom: 12px;
  object-fit: cover;
}

.content__title {
  font-size: 18px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -moz-box;
  -moz-box-orient: vertical;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  line-clamp: 2;
  box-orient: vertical;
}

.content__text {
  flex-grow: 1;
  margin-bottom: 10px;
  font-size: 12px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -moz-box;
  -moz-box-orient: vertical;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  line-clamp: 2;
  box-orient: vertical;
}

.content__date {
  position: relative;
  display: block;
  max-width: 200px;
  margin-left: auto;
  padding-left: 18px;
}

.content__date::before {
  content: "";
  position: absolute;
  display: block;
  left: 4px;
  bottom: 8px;
  border-radius: 50%;
  width: 8px;
  height: 8px;
  background-color: #009688;
}

.slider {
  margin: 15px 0 45px;
}

.slider__container {
  margin-left: 10px;
}

.slider__item {
  max-width: 230px;
  max-height: 400px;
  width: auto;
  overflow: hidden;
}

@media only screen and (max-width: 1023px) {
  .slider__item {
    max-height: 250px;
  }
}

.slider__img {
  min-width: 100%;
  height: 320px;
  margin-right: 10px;
  object-fit: cover;
}

.news {
  border: 4px solid #125D98;
  border-bottom: 0px;
  padding: 25px 25px 55px 25px;
  border-radius: 12px 12px 0 0;
  background-color: #fff;
}

.news__item:not(:last-child) {
  margin-bottom: 25px;
}

.news__element {
  position: relative;
  display: flex;
  flex-direction: row;
  padding: 20px;
  border-radius: 6px;
  background-image: linear-gradient(to top, #f3f9fb, #eaf6fb, #e2f3fb, #daf0fc, #d2edfd, #d1edfe, #d1ecfe, #d0ecff, #d5eeff, #dbf0ff, #e0f2ff, #e5f4ff);
  transition: transform 0.4s, box-shadow 0.4s !important;
}

.news__element:hover {
  box-shadow: 0 5px 20px 5px rgba(0, 0, 0, 0.4);
  transform: translateY(-4px) !important;
}

.news__image {
  margin-right: 20px;
  max-width: 40%;
  max-height: 100%;
  object-fit: cover;
}

.news__link {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  cursor: pointer;
}

.news__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.news__title {
  margin-bottom: 15px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -moz-box;
  -moz-box-orient: vertical;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  line-clamp: 2;
  box-orient: vertical;
}

.news__text {
  flex-grow: 1;
  margin-bottom: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -moz-box;
  -moz-box-orient: vertical;
  display: -webkit-box;
  -webkit-line-clamp: 11;
  -webkit-box-orient: vertical;
  line-clamp: 11;
  box-orient: vertical;
}

.news__date {
  position: relative;
  display: block;
  text-align: right;
  max-width: 200px;
  margin-left: auto;
  padding-left: 18px;
}

.news__date::before {
  content: "";
  position: absolute;
  display: block;
  right: 84px;
  bottom: 8px;
  border-radius: 50%;
  width: 8px;
  height: 8px;
  background-color: #009688;
}

.product__content {
  margin: 45px 0;
  padding: 25px 0;
  border-radius: 12px 12px 0 0;
  background-color: #fff;
}

.product__title {
  margin: 0 20px;
  text-align: center;
}

.product__text {
  float: left;
  border-radius: 12px;
  margin: 15px 15px 15px 0;
  padding: 25px;
  min-width: 100%;
  background-color: #fff;
}

.product__img {
  float: left;
  object-fit: cover;
  margin: 0 15px 8px 0;
  min-width: 30vw;
  max-width: 31vw;
  min-height: 20vw;
  max-height: 21vw;
}

.author {
  padding: 0 15px;
}

.author__container {
  border: 4px solid #125D98;
  max-width: 1300px;
  margin: 25px auto;
  padding: 25px;
  border-radius: 12px;
  background-color: #fff;
}

.author__top {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin-bottom: 30px;
  padding: 0 50px;
}

.author__img {
  object-fit: cover;
  object-position: 20% 50%;
  width: 380px;
  height: 380px;
  border-radius: 50%;
}

.author__body {
  padding-left: 20px;
}

.author__name {
  margin-bottom: 10px;
}

.author__year {
  display: block;
  margin-bottom: 15px;
}

.author__block {
  border-radius: 0 20px 20px 0;
  margin: 0;
  background: #eaeaea;
  color: #125D98;
  padding: 30px 30px 30px 80px;
  position: relative;
  font-family: "Lato", sans-serif;
  background-image: linear-gradient(to top, #f3f9fb, #eaf6fb, #e2f3fb, #daf0fc, #d2edfd, #d1edfe, #d1ecfe, #d0ecff, #d5eeff, #dbf0ff, #e0f2ff, #e5f4ff);
}

.author__block:before {
  content: "\1F676";
  position: absolute;
  z-index: 2;
  left: 30px;
  top: 15px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  text-align: center;
  line-height: 80px;
  color: #f5e9cf;
  font-size: 50px;
  background: rgba(188, 231, 250, 0.7);
}

.author__block:after {
  content: "";
  position: absolute;
  left: 34px;
  top: 19px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: rgba(242, 124, 176, 0.7);
}

.author__block p {
  position: relative;
  z-index: 3;
  font-size: 18px;
  letter-spacing: 0.05em;
  line-height: 1.4;
  margin: 0 0 16px;
  color: #125D98;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -moz-box;
  -moz-box-orient: vertical;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  line-clamp: 3;
  box-orient: vertical;
}

.author__content {
  display: flex;
  justify-content: end;
  flex-wrap: wrap;
  margin: 15px 20px;
  list-style: decimal;
}

.author__item {
  min-width: 30%;
}

.author__item::marker {
  color: #125D98;
}

.author__item:not(:last-child) {
  margin-bottom: 12px;
}

.author__link {
  display: inline-block;
  width: 320px;
  font-size: 18px;
  color: #243f4d;
  transition: color 0.3s, transform 0.4s;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -moz-box;
  -moz-box-orient: vertical;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  line-clamp: 3;
  box-orient: vertical;
}

.author__link:hover {
  transform: translateX(-2px);
  color: #125D98;
}

.footer {
  max-height: 60px;
  background-color: #125D98;
}

.footer__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 60px;
}

.footer__copy {
  color: #fff;
}
